import VaultsListH5 from '@/components/vaults-v2/list/H5/VaultsListH5'
import { VaultsListPC } from '@/components/vaults-v2/list/PC/VaultsListPC'
import { VaultsBanner } from '@/components/vaults-v2/list/VaultsBanner'
import { VaultsTabFilter } from '@/components/vaults-v2/list/VaultsTabFilter'
import useGetVaultsContract from '@/hooks/vault-v2/useGetVaultsContract'
import useVaultList from '@/hooks/vault-v2/useVaultList'
import useVaultsListV2Store from '@/store/vaults-v2/useVaultsList'
import useVaultsPositionStore from '@/store/vaults-v2/useVaultsPosition'
import { QueryVaultListOptions } from '@/types/vaults'
import { Tab } from '@cetus/design/src/components/common/SelectTab'
import { SortDropType } from '@cetus/design/src/components/common/SortDropBlock'
import { useAccountBalance, useInterval } from '@cetus/hooks'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { useAccountStore } from '@cetus/stores'
import { Token } from '@cetus/types'
import { Box, VStack } from '@chakra-ui/react'
import { useEffect, useMemo, useState } from 'react'

export default function VaultsV2() {
  const { vaultPageList, vaultListLoading, setVaultListLoading, vaultsList, vaultsTokenList } = useVaultsListV2Store()
  const { fetchAccountBalance } = useAccountBalance()
  const { currentAccount } = useAccountStore()
  const { getCurrentTabNum, fetchVaultList, filterVaultList, sortVaultList } = useVaultList()

  const [isYourHoldings, setIsYourHoldings] = useState(false)
  const [selectCoinList, setSelectCoinList] = useState<Token[]>([])

  const { clearVaultsPositionObj } = useVaultsPositionStore()

  const [currTab, setCurrTab] = useState<Tab<object>>({
    label: 'All Vaults',
    value: 'all'
  })

  const [queryOptions, setQueryOptions] = useState<QueryVaultListOptions>({
    sortRule: 'desc',
    sortType: 'tvl'
  })

  const sortByList: SortDropType[] = [
    { label: 'APY', value: 'apr' },
    { label: 'TVL', value: 'tvl' }
  ]
  const [currSortType, setCurrSortType] = useState<SortDropType>(sortByList[1])

  const tabList = useMemo(() => {
    const list: Tab<object>[] = [
      {
        label: 'All Vaults',
        value: 'all',
        num: getCurrentTabNum({ sortOptions: queryOptions, currentTab: 'all', isYourHoldings, selectCoinList })
      },
      {
        label: 'Cetus',
        value: 'cetus',
        num: getCurrentTabNum({ sortOptions: queryOptions, currentTab: 'cetus', isYourHoldings, selectCoinList })
      },
      {
        label: 'Haedal',
        value: 'haedal',
        num: getCurrentTabNum({ sortOptions: queryOptions, currentTab: 'haedal', isYourHoldings, selectCoinList })
      }
    ]
    return list
  }, [JSON.stringify(vaultsList), isYourHoldings, JSON.stringify(selectCoinList)])

  // ÂàùÂßãÂåñÊãâÂèñÊï∞ÊçÆ
  useEffect(() => {
    fetchVaultList({ sortOptions: queryOptions, currentTab: currTab.value as string, isYourHoldings, selectCoinList })
  }, [])

  useEffect(() => {
    if (!currentAccount?.address) {
      setIsYourHoldings(false)
      clearVaultsPositionObj()
    }
  }, [currentAccount?.address])

  const [refreshCount, setRefreshCount] = useState<number>(0)
  useInterval({
    interval: 60000,
    callback: () => {
      setRefreshCount(refreshCount + 1)
      if (refreshCount >= 60) {
        setRefreshCount(0)
        fetchVaultList({ sortOptions: queryOptions, currentTab: currTab.value as string, isYourHoldings, selectCoinList }, false)
        // if (currentAccount?.address) {
        //   getVaultsPosition(vaultsList)
        // }
      }
    }
  })

  const handleTabChange = (tab: Tab<object>) => {
    setRefreshCount(0)
    if (currTab.value === tab.value || vaultListLoading) return
    setCurrTab(tab)
    filterVaultList({ sortOptions: queryOptions, currentTab: tab.value as string, isYourHoldings, selectCoinList })
    setVaultListLoading(true)
    setTimeout(() => {
      setVaultListLoading(false)
    }, 200)
  }

  const handleSortChange = (type: SortDropType) => {
    setRefreshCount(0)
    const updated = { ...queryOptions, sortType: type.value }
    setCurrSortType(type)
    setQueryOptions(updated)
    sortVaultList(updated, vaultPageList)
  }

  const handleSortRuleChange = (sortRule: 'asc' | 'desc') => {
    setRefreshCount(0)
    const updated = { ...queryOptions, sortRule }
    setQueryOptions(updated)
    sortVaultList(updated, vaultPageList)
  }

  const handleSelectCoin = (tokenInfo: Token) => {
    setRefreshCount(0)
    setSelectCoinList(prev => {
      const exists = prev.some(ele => ele?.coin_type === tokenInfo?.coin_type)
      if (exists) return prev
      const updated = [...prev, tokenInfo]
      filterVaultList({ sortOptions: queryOptions, currentTab: currTab.value as string, isYourHoldings, selectCoinList: updated })
      return updated
    })
  }

  const handleDeleteCoin = (tokenInfo: Token) => {
    setRefreshCount(0)
    setSelectCoinList(prev => {
      const updated = prev.filter(ele => ele?.coin_type !== tokenInfo?.coin_type)
      filterVaultList({ sortOptions: queryOptions, currentTab: currTab.value as string, isYourHoldings, selectCoinList: updated })
      return updated
    })
  }

  const handleToggleHoldings = () => {
    setRefreshCount(0)
    const newValue = !isYourHoldings
    setIsYourHoldings(newValue)
    filterVaultList({ sortOptions: queryOptions, currentTab: currTab.value as string, isYourHoldings: !isYourHoldings, selectCoinList })
  }

  const handleRefresh = () => {
    setRefreshCount(0)
    setVaultListLoading(true)
    fetchAccountBalance()
    fetchVaultList({ sortOptions: queryOptions, currentTab: currTab.value as string, isYourHoldings, selectCoinList })
  }

  const { getVaultsContractInfo } = useGetVaultsContract()

  useEffect(() => {
    if (currentAccount?.address) {
      getVaultsContractInfo(vaultsList)
    }
  }, [currentAccount?.address, vaultsList.length])

  const { isApp } = useWindowWidth()

  return (
    <VStack gap="0px" w="100%">
      <VaultsBanner />
      <Box h={{ base: '260px', lg: '240px' }} />
      <VaultsTabFilter
        sortByList={sortByList}
        currTab={currTab}
        tabList={tabList}
        whiteTokenList={vaultsTokenList}
        selectCoinList={selectCoinList}
        onClickSelectCoinList={handleSelectCoin}
        onDeleteSelectCoinList={handleDeleteCoin}
        showSkeletonLoading={vaultListLoading}
        handleChangeTab={handleTabChange}
        currSortType={currSortType}
        onSortByChange={handleSortChange}
        sortRule={queryOptions.sortRule}
        handleChangeSortRule={handleSortRuleChange}
        handleRefresh={handleRefresh}
        isYourHoldings={isYourHoldings}
        onClickIsYourHoldings={handleToggleHoldings}
      />
      {isApp ? (
        <VaultsListH5 isShowPowered={currTab.label !== 'Haedal'} dataList={vaultPageList} showSkeletonLoading={vaultListLoading} />
      ) : (
        <VaultsListPC isShowPowered={currTab.label !== 'Haedal'} dataList={vaultPageList} showSkeletonLoading={vaultListLoading} />
      )}
    </VStack>
  )
}
