import useGetVaultsPosition from '@/hooks/vault-v2/useGetVaultsPosition'
import useVaultsListV2Store from '@/store/vaults-v2/useVaultsList'
import useVaultsPoolContractStore from '@/store/vaults-v2/useVaultsPoolContract'
import { VaultsV2ListItemProps } from '@/types/vaultsV2'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { useAccountStore } from '@cetus/stores'
import { useEffect, useMemo, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import VaultsListItemH5 from './H5/VaultsListItemH5'
import { VaultsListItemPC } from './PC/VaultsListItemPC'

function VaultsListItem(props: VaultsV2ListItemProps) {
  const navigate = useNavigate()
  const { apiInfo, isShowPowered } = props
  const { lpTokenInfoObj } = useVaultsListV2Store()
  const { currentAccount } = useAccountStore()
  const lpTokenInfo = useMemo(() => {
    return lpTokenInfoObj[apiInfo?.lpTokenType]
  }, [apiInfo?.lpTokenType, JSON.stringify(lpTokenInfoObj)])

  const [openExpend, setOpenExpend] = useState<boolean>(false)

  const jumpVaultsDetail = (vaultId: string) => {
    navigate(`/vaults-detail/${vaultId}/${apiInfo?.category}`)
  }

  const onExpand = () => {
    setOpenExpend(!openExpend)
  }

  const { getVaultPosition } = useGetVaultsPosition()
  const { lstVaultContractInfoObj, haedalVaultContractInfoObj, vaultClmmPoolContractInfoObj } = useVaultsPoolContractStore()
  const lstVaultContractInfo = useMemo(() => {
    return lstVaultContractInfoObj[apiInfo?.vaultId]
  }, [apiInfo?.vaultId, JSON.stringify(lstVaultContractInfoObj)])
  const haedalVaultContractInfo = useMemo(() => {
    return haedalVaultContractInfoObj[apiInfo?.vaultId]
  }, [apiInfo?.vaultId, JSON.stringify(haedalVaultContractInfoObj)])
  const vaultClmmPoolContractInfo = useMemo(() => {
    return vaultClmmPoolContractInfoObj[apiInfo?.clmmPoolAddress]
  }, [apiInfo?.clmmPoolAddress, JSON.stringify(vaultClmmPoolContractInfoObj)])
  useEffect(() => {
    if (currentAccount?.address) {
      const vaultContractInfo = apiInfo?.category == 'cetus' ? lstVaultContractInfo : haedalVaultContractInfo
      if (vaultContractInfo && vaultClmmPoolContractInfo) {
        getVaultPosition(apiInfo, vaultClmmPoolContractInfo, vaultContractInfo)
      }
    }
  }, [currentAccount?.address, apiInfo?.vaultId, apiInfo?.clmmPoolAddress, lstVaultContractInfo, haedalVaultContractInfo, vaultClmmPoolContractInfo])

  const { isApp } = useWindowWidth()

  const host = window.location.host
  const isShowAumLimit = useMemo(() => {
    return !host.includes('app.cetus.zone') && !host.includes('cetus-cmm-pro.netlify.app') && !host.includes('localhost:5173')
  }, [host])

  return isApp ? (
    <VaultsListItemH5
      openExpend={openExpend}
      onExpand={onExpand}
      apiInfo={apiInfo}
      logo_url={lpTokenInfo?.logo_url}
      jumpVaultsDetail={jumpVaultsDetail}
      isShowPowered={isShowPowered}
      isShowAumLimit={isShowAumLimit}
    />
  ) : (
    <VaultsListItemPC
      openExpend={openExpend}
      onExpand={onExpand}
      apiInfo={apiInfo}
      logo_url={lpTokenInfo?.logo_url}
      jumpVaultsDetail={jumpVaultsDetail}
      isShowPowered={isShowPowered}
      isShowAumLimit={isShowAumLimit}
    />
  )
}

export default VaultsListItem
